header {
  background: $herobgcolor;
  display: flex;
  flex-direction: column;

  #bgVideo {
    position: absolute;
    right: 0;
    bottom: 0;
    min-width: 100%;
    min-height: 100%;
    z-index: 5; }

  // nested flexbox container
  .top-bar {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    z-index: 10;
    background: rgba(34,34,34,0.9);

    .logo {
      margin: 10px;
      height: 60px;
      width: 60px;
      border-radius: 100%;
      background: $profileimg;
      background-size: cover; }

    nav {
      flex: 1;
      display: flex;
      justify-content: flex-end;
      align-items: baseline;

      a {
        text-transform: uppercase;
        margin: 0 20px 0 0;

        &:last-child {
          margin-right: 20px; } } } }

  // nested flexbox container
  .hero {
    height: calc(100vh - 80px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: $brand-primary;
    text-align: center;
    z-index: 10;
    background: rgba(34,34,34,0.9);

    h1 {
      border: 6px solid $brand-primary;
      text-transform: uppercase;
      padding: 15px 20px;
      margin: 0;
      line-height: 1;
      letter-spacing: 0.02em;
      font-size: 30px; }

    .kicker {
      margin: 10px 0;
      display: flex;

      & > *::after {
        content: "//";
        margin: 0 6px; }

      & > *:last-child::after {
        content: "";
        margin: 0; } } } }
