.work-flexwrap {
  max-width: 800px;
  align-self: center;
  @extend %flexwrap;

  [class^="work-"] {
    display: flex;
    margin: 0 10px;
    background: white center no-repeat;
    background-size: cover;

    &:hover, &:focus, &:active {

      div {
        background: rgba(darken($brand-primary, 50%), 0.8);
        transition: background 0.2s;

        & > * {
          opacity: 1; } } }

    div {
      overflow: hidden;
      height: 130px;
      width: 200px;
      text-align: center;
      // nested flex container
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;
      justify-content: center;
      align-items: center; }

    div > * {
      opacity: 0;
      font-size: 1.2em; } }

  .work-mac {
    background-image: url("/assets/img/work-mac.png"); }
  .work-pcs {
    background-image: url("/assets/img/work-pcs.png"); }
  .work-scitt {
    background-image: url("/assets/img/work-scitt.png"); } }
