section {
  background: white;
  padding: 100px 0;
  display: flex;
  flex-direction: column;

  &:nth-of-type(even) {
    background: $bgcolor; }

  h2 {
    margin: 0;
    color: $brand-primary;
    text: {
      align: center;
      transform: uppercase; }
    font-size: 18px; } }

%flexwrap {
  margin: 40px 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center; }
